.header {
    position: relative;
    &__in {
        max-width: 1196 + 40 + 100 +  px;
        position: relative;
        margin: 0 auto;
        @include rmin(601) {
            @include clr; } }
    &__desc {
        background-color: $gold;
        position: relative;
        z-index: 1; }
    &__pic {
        float: left;
        width: 116px;
        position: relative;
        right: 40px;
        margin-left: 50px;
        margin-bottom: -5px;
        img {
            max-width: 100%; }
        @include r($mob-m) {
            width: 84px;
            height: 147px;
            float: none;
            margin: 0 auto;
            right: 0; }
        @include r(mob-s) {
            width: 68px;
            height: 152px; } }
    &__col-txt {
        padding: 24px 0;
        color: #fff; } }
.top-nav {
    background-color: #fff;
    box-shadow: 0px 5px 25px 0px rgba(100,100,100,0.5);
    &__bl {
        border-bottom: 1px solid $gold;
        padding-top: 30px;
        @include r($mob-m) {
            padding-top: 10px; }
        &_in {
            max-width: 1518px;
            text-align: right;
            @include clr;
            @include r($desktop-s) {
                max-width: 1222px; } } }
    &__info {
        display: inline-block;
        text-align: center;
        @include r($mob-m) {
            float: left;
            margin-left: 10px; } }
    &__phone {
        display: block;
        @include helv-m;
        font-size: 18px;
        color: $brown;
        text-decoration: none;
        @include r($mob-m) {
            font-size: 14px; } }
    &__btn {
        display: inline-block;
        padding: 13px 15px;
        background-color: $d-red; }
    &__link {
        font-size: 14px;
        color: #fff;
        &:hover {
            opacity: 0.8; }
        @include r($mob-m) {
            font-size: 12px; } }
    &__versions {
        display: inline-block;
        margin-left: 5px;
        margin-right: 20px; }
    &__version {
        display: block;
        font-size: 14px;
        color: $l-gray;
        text-decoration: none; }
    &__burger {
        display: none;
        width: 50px;
        height: 50px;
        margin-right: 10px;
        background-color: transparent;
        border: 1px solid $gold;
        .icon {
            width: 100%;
            height: 100%;
            stroke: $gold;
            stroke-width: 5; }
        @include r($mob-l) {
            display: inline-block; } } }

.logo {
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 100;
    transform: translate(-50%);
    display: block;
    width: 200px;
    height: 200px;
    background-color: $gold;
    border: 1px solid #fff;
    border-top: none;
    &__img {
        @include vcenter;
        display: block;
        width: 154px;
        height: 50px;
        @include r($mob-m) {
            width: 100px;
            height: 25px; } }
    &__el {
        fill: #fff; }
    @include r($tablet) {
        left: 0;
        top: 0;
        transform: none;
        height: 98px;
        background-color: transparent;
        border: none;
        &__el {
            fill: $gold; } }
    @include r($mob-m) {
        display: none; } }

.top-menu {
    max-width: 1080px;
    margin: 0 auto;
    text-align: center;
    @include clr;
    &__col {
        width: percentage(440/1080);
        &_l {
            float: left;
            @include r($tablet) {
                margin-right: -20px; } }
        &_r {
            float: right; }
        @include r($tablet) {
            float: none;
            display: inline-block;
            width: 49%; }
        @include r($mob-l) {
            display: block;
            width: 100%; } }
    &__item {
        display: inline-block;
        padding: 22px 0;
        width: 32%;
        @include r($mob-s) {
            width: 100%;
            display: block;
            padding: 15px 0; } }
    &__link {
        color: $gold;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 12px;
        &:hover, &.is-active {
            color: #191821; } } }
.top-slider {
    position: relative;
    &__slide {
        width: 100%;
        &_img {
            max-width: 100%; } }
    &__arr {
        display: block;
        background-color: transparent;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        z-index: 5;
        opacity: 0.8;
        .icon {
            width: 50px;
            height: 50px;
            fill: #fff; }
        &_prev {
            left: 0; }
        &_next {
            right: 0; }
        &:hover {
            opacity: 1; } } }
.entry-popup {
    width: 100%;
    max-width: 650px;
    &__btn {
        background-color: transparent;
        right: 152px;
        top: 40px; } }



